@import url(https://fonts.googleapis.com/css2?family=Ubuntu&display=swap);
html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@media screen and (max-width: 560px) {
  html {
    font-size: 50%;
  }
}

@media screen and (max-width: 428px) {
  html {
    font-size: 45%;
  }
}

@media screen and (max-width: 384px) {
  html {
    font-size: 35%;
  }
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.navbar {
  background-color: #2bbbe7;
  position: -webkit-sticky;
  position: sticky;
  top: 0;
  z-index: 10000;
}

.navbar__logo {
  font-size: 5rem;
  font-weight: 600;
  margin: 10px;
}

.navbar__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 90%;
  margin: auto;
}

.navbar__list {
  display: flex;
}

.navbar__listItem {
  margin-left: 1rem;
}

.navbar__listItem button {
  width: 15rem;
  height: 4rem;
  font-size: 1.2rem;
}

@media screen and (max-width: 1054px) {
  .navbar {
    padding: 2rem;
    position: static;
  }
  .navbar__wrapper {
    flex-direction: column;
    align-items: center;
  }

  .navbar__list {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    /* border: 4px solid; */
  }
  .navbar__listItem {
    margin-top: 0.5rem;
  }
  .navbar__listItem button {
    width: 100vw;
    font-size: 1.5rem;
  }
}

.upper__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 5rem;
}

.upper__link,
.upper__link2 {
  width: 80%;
  font-size: 1.5rem;
  font-weight: 600;
  cursor: pointer;
  color: rgba(0, 0, 0, 0.5);
  transition: all 0.2s linear;
}

.upper__link {
  margin: 1.5rem 0;
}

.upper__link2 {
  text-decoration: underline;
}

.upper__link:hover,
.upper__link2:hover {
  color: black;
}

.upper__wrapper {
  width: 80%;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
}

.upper__left {
  padding: 1rem;
  width: 55%;
  max-height: 100rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: flex-start;
}

.upper__leftH {
  font-size: 4rem;
  font-weight: 600;
  margin-bottom: 2rem;
}

.upper__lefth {
  font-size: 2.4rem;
  font-weight: 500;
  margin-bottom: 2rem;
}

.upper__leftP1,
.upper__leftP2 {
  font-size: 1.8rem;
  margin-bottom: 2rem;
  color: rgba(0, 0, 0, 0.5);
}

.upper__right {
  width: 38%;
  height: 50rem;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}

.upper__right > img {
  width: 100%;
}

.upper__rightRow {
  display: flex;
  justify-content: space-between;
  width: 80%;
}

.upper__rightRowItem {
  font-size: 1.5rem;
}

.upper__rightRowItem span {
  color: rgba(0, 0, 0, 0.5);
}

.upper__rightButton {
  width: 90%;
  margin: 0 auto;
}
.upper__rightButton button {
  width: 100%;
  height: 4rem;
  background-color: rgb(63, 107, 230);
  font-size: 1.6rem;
  font-weight: 600;
  text-transform: none;
}

.upper__rightButton button:hover {
  background-color: rgba(63, 108, 230, 0.774);
}

@media screen and (min-width: 701px) and (max-width: 910px) {
  .upper__wrapper {
    justify-content: space-between;
  }
  .upper__right {
    width: 60%;
  }
  .upper__left {
    width: 40%;
  }
}

@media screen and (max-width: 700px) {
  .upper__right {
    display: none;
  }
  .upper__left {
    width: 100%;
  }
}

.carousel {
  margin: 15rem 0;
}

.carousel__containerHead,
.carousel__bar {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 80%;
  margin: auto;
}

.carousel__containerHead {
  justify-content: center;
}

.carousel__head {
  display: flex;
  align-items: center;
}

.carousel__bullet {
  background-color: rgba(177, 164, 238, 0.486);
  width: 4rem;
  color: rgba(0, 0, 255, 0.473);
  height: 4rem;
  border-radius: 3rem;
  text-align: center;
  line-height: 4rem;
  font-size: 2rem;
}

.carousel__text {
  margin-left: 2rem;
}

.carousel__text h1 {
  font-size: 2.5rem;
}

.carousel__text p {
  font-size: 1.5rem;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.5);
}

/* bar css */

.carousel__box {
  margin: 2.5rem 3rem 2.5rem 0;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 1rem;
  width: 20rem;
  cursor: pointer;
  border-bottom: 1px solid grey;
}

.carousel__text2 h2 {
  font-size: 2rem;
  font-weight: 500;
  margin-top: 2rem;
}

.carousel__text2 p {
  font-size: 1.4rem;
  font-weight: 600;
  color: rgba(0, 0, 0, 0.5);
}

.carousel__box:hover {
  box-shadow: 0.5px 0.5px 1px 0px rgba(0, 0, 0, 0.13),
    -0.5px 0.5px 1px 0px rgba(0, 0, 0, 0.158);
}

.carousel__image {
  border: 3px solid;
}

.carousel__image > img {
  width: 100%;
}

.count__wrapper {
  width: 85%;
  margin: 15rem auto 10rem auto;
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
}

.count__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  width: 25rem;
  height: 20rem;
  margin: 1rem;
  /* border: 4px solid rgb(60, 193, 211); */
  background-color: #2bbbe7;
  text-align: center;
  line-height: 100%;
  box-shadow: 0 0 15px 2px black;
}

.count__itemText {
  font-size: 2.5rem;
  margin-top: 1rem;
  font-weight: 600;
}

.media__wrapper {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  width: 100%;
  margin: 5rem auto;
  background-color: #2bbbe7;
}

.media__header {
  font-size: 5rem;
  font-weight: 700;
  text-transform: capitalize;
  text-align: center;
}

.media__title {
  font-size: 2rem;
  font-weight: 700;
  text-transform: uppercase;
  text-align: center;
  margin: 1rem;
}

.media__item {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 30rem;
  height: 25rem;
  margin: 1rem;
}

.site-footer {
  background-color: #2bbbe7;
  padding: 45px 0 20px;
  font-size: 15px;
  line-height: 24px;
  color: black;
}
.site-footer hr {
  border-top-color: #bbb;
  opacity: 0.5;
}
.site-footer hr.small {
  margin: 20px 0;
}
.site-footer h6 {
  color: black;
  font-weight: 700;
  font-size: 16px;
  text-transform: uppercase;
  margin-top: 5px;
  letter-spacing: 2px;
}
.site-footer a {
  color: black;
}
.site-footer a:hover {
  color: #3366cc;
  text-decoration: none;
}
.footer-links {
  padding-left: 0;
  list-style: none;
}
.footer-links li {
  display: block;
}
.footer-links a {
  color: black;
}
.footer-links a:active,
.footer-links a:focus,
.footer-links a:hover {
  color: #3366cc;
  text-decoration: none;
}
.footer-links.inline li {
  display: inline-block;
}
.site-footer .social-icons {
  text-align: right;
}
.site-footer .social-icons a {
  width: 40px;
  height: 40px;
  line-height: 40px;
  margin-left: 6px;
  margin-right: 0;
  border-radius: 100%;
  background-color: #33353d;
}
.copyright-text {
  margin: 0;
}
@media (max-width: 991px) {
  .site-footer [class^="col-"] {
    margin-bottom: 30px;
  }
}
@media (max-width: 767px) {
  .site-footer {
    padding-bottom: 0;
  }
  .site-footer .copyright-text,
  .site-footer .social-icons {
    text-align: center;
  }
}
.social-icons {
  padding-left: 0;
  margin-bottom: 0;
  list-style: none;
}
.social-icons li {
  display: inline-block;
  margin-bottom: 4px;
}
.social-icons li.title {
  margin-right: 15px;
  text-transform: uppercase;
  color: #96a2b2;
  font-weight: 700;
  font-size: 13px;
}
.social-icons a {
  background-color: #eceeef;
  color: #818a91;
  font-size: 16px;
  display: inline-block;
  line-height: 44px;
  width: 44px;
  height: 44px;
  text-align: center;
  margin-right: 8px;
  border-radius: 100%;
  transition: all 0.2s linear;
}
.social-icons a:active,
.social-icons a:focus,
.social-icons a:hover {
  color: #fff;
  background-color: #29aafe;
}
.social-icons.size-sm a {
  line-height: 34px;
  height: 34px;
  width: 34px;
  font-size: 14px;
}
.social-icons a.facebook:hover {
  background-color: #3b5998;
}
.social-icons a.twitter:hover {
  background-color: #00aced;
}
.social-icons a.linkedin:hover {
  background-color: #007bb6;
}
.social-icons a.dribbble:hover {
  background-color: #ea4c89;
}
@media (max-width: 767px) {
  .social-icons li.title {
    display: block;
    margin-right: 0;
    font-weight: 600;
  }
}

* {
  font-family: "Ubuntu", sans-serif;
}

.login,
.login__wrapper,
.detailForm {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.login {
  min-height: 100vh;
}
.login__wrapper {
  width: 50rem;
  height: 60rem;
  background-color: rgb(60, 185, 243);
  position: relative;
  border-radius: 1rem;
  box-shadow: 0 0 1rem 0.2rem rgb(7, 41, 73);
  flex-basis: 1;
}

.login__brand {
  font-size: 5rem;
  font-weight: 700;
  position: absolute;
  top: 3rem;
  color: rgba(0, 0, 0, 0.692);
}

.login__head {
  font-size: 3rem;
  font-weight: 600;
  position: absolute;
  z-index: 100;
  top: 8rem;
}

::-webkit-input-placeholder {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  font-weight: 600;
}

:-ms-input-placeholder {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  font-weight: 600;
}

::placeholder {
  font-size: 1.5rem;
  color: rgb(0, 0, 0);
  font-weight: 600;
}

.detailForm {
  width: 100%;
}

.detailForm input {
  width: 80%;
  height: 4rem;
  margin-top: 3rem;
  outline: none;
  border: none;
  background-color: aliceblue;
  /* border-radius: 3px; */
  font-size: 1.8rem;
  padding: 1rem;
  background-color: rgb(60, 185, 243);
  border-bottom: 1px solid;
}

.detailForm button {
  margin-top: 4rem;
  width: 40%;
  height: 3.5rem;
  font-size: 2rem;
  line-height: 100%;
  border-radius: 0.4rem;
  font-weight: 600;
  border: 1px solid rgb(19, 111, 153);
  transition: all 0.2s linear;
}

.detailForm button:hover {
  /* background-color: rgb(172, 210, 230); */
  color: rgb(19, 111, 153);
  box-shadow: 0 0 1rem 0.2rem rgb(36, 21, 121);
}

.login__link {
  margin-top: 2.5rem;
  font-size: 1.5rem;
  font-weight: 600;
  color: black;
}

.login__link:hover {
  text-decoration: underline;
  cursor: pointer;
}

